/**
 * TREXIMA v4.0 - Export Configuration Component
 *
 * Configure export options including locales, picklists, MDF objects, and FO translations.
 */

import { useEffect, useState } from 'react';
import { Settings, Languages, CheckCircle, Loader2, Save } from 'lucide-react';
import { useProjectStore } from '../../store/projectStore';
import projectsApi from '../../api/projects';
import type { ECObject, FOTranslationType } from '../../types';

interface ExportConfigProps {
  projectId: string;
}

const AVAILABLE_LOCALES = [
  { code: 'en_US', name: 'English (US)', group: 'English' },
  { code: 'en_GB', name: 'English (UK)', group: 'English' },
  { code: 'de_DE', name: 'German (Germany)', group: 'German' },
  { code: 'de_AT', name: 'German (Austria)', group: 'German' },
  { code: 'de_CH', name: 'German (Switzerland)', group: 'German' },
  { code: 'fr_FR', name: 'French (France)', group: 'French' },
  { code: 'fr_CA', name: 'French (Canada)', group: 'French' },
  { code: 'fr_BE', name: 'French (Belgium)', group: 'French' },
  { code: 'fr_CH', name: 'French (Switzerland)', group: 'French' },
  { code: 'es_ES', name: 'Spanish (Spain)', group: 'Spanish' },
  { code: 'es_MX', name: 'Spanish (Mexico)', group: 'Spanish' },
  { code: 'it_IT', name: 'Italian (Italy)', group: 'Italian' },
  { code: 'pt_PT', name: 'Portuguese (Portugal)', group: 'Portuguese' },
  { code: 'pt_BR', name: 'Portuguese (Brazil)', group: 'Portuguese' },
  { code: 'nl_NL', name: 'Dutch (Netherlands)', group: 'Dutch' },
  { code: 'nl_BE', name: 'Dutch (Belgium)', group: 'Dutch' },
  { code: 'pl_PL', name: 'Polish', group: 'Eastern European' },
  { code: 'cs_CZ', name: 'Czech', group: 'Eastern European' },
  { code: 'hu_HU', name: 'Hungarian', group: 'Eastern European' },
  { code: 'ro_RO', name: 'Romanian', group: 'Eastern European' },
  { code: 'ru_RU', name: 'Russian', group: 'Eastern European' },
  { code: 'uk_UA', name: 'Ukrainian', group: 'Eastern European' },
  { code: 'ja_JP', name: 'Japanese', group: 'Asian' },
  { code: 'zh_CN', name: 'Chinese (Simplified)', group: 'Asian' },
  { code: 'zh_TW', name: 'Chinese (Traditional)', group: 'Asian' },
  { code: 'ko_KR', name: 'Korean', group: 'Asian' },
  { code: 'th_TH', name: 'Thai', group: 'Asian' },
  { code: 'vi_VN', name: 'Vietnamese', group: 'Asian' },
  { code: 'id_ID', name: 'Indonesian', group: 'Asian' },
  { code: 'ms_MY', name: 'Malay', group: 'Asian' },
  { code: 'ar_SA', name: 'Arabic (Saudi Arabia)', group: 'Middle East' },
  { code: 'he_IL', name: 'Hebrew', group: 'Middle East' },
  { code: 'tr_TR', name: 'Turkish', group: 'Middle East' },
  { code: 'el_GR', name: 'Greek', group: 'European' },
  { code: 'sv_SE', name: 'Swedish', group: 'Scandinavian' },
  { code: 'no_NO', name: 'Norwegian', group: 'Scandinavian' },
  { code: 'da_DK', name: 'Danish', group: 'Scandinavian' },
  { code: 'fi_FI', name: 'Finnish', group: 'Scandinavian' },
  { code: 'hi_IN', name: 'Hindi', group: 'Indian' },
  { code: 'bn_IN', name: 'Bengali', group: 'Indian' },
  { code: 'ta_IN', name: 'Tamil', group: 'Indian' },
  { code: 'te_IN', name: 'Telugu', group: 'Indian' },
];

export default function ExportConfig({ projectId }: ExportConfigProps) {
  const { currentProject, updateConfig, isSaving } = useProjectStore();

  // Export options
  const [selectedLocales, setSelectedLocales] = useState<string[]>(['en_US']);
  const [exportPicklists, setExportPicklists] = useState(false);
  const [exportMDFObjects, setExportMDFObjects] = useState(false);
  const [exportFOTranslations, setExportFOTranslations] = useState(false);

  // Object selections
  const [ecObjects, setEcObjects] = useState<ECObject[]>([]);
  const [selectedECObjects, setSelectedECObjects] = useState<string[]>([]);
  const [foTranslationTypes, setFoTranslationTypes] = useState<FOTranslationType[]>([]);
  const [selectedFOTypes, setSelectedFOTypes] = useState<string[]>([]);

  const [isLoading, setIsLoading] = useState(false);
  const [saveSuccess, setSaveSuccess] = useState(false);

  // Load config from project
  useEffect(() => {
    if (currentProject?.config) {
      const config = currentProject.config;
      if (config.locales) setSelectedLocales(config.locales);
      if (config.export_picklists !== undefined) setExportPicklists(config.export_picklists);
      if (config.export_mdf_objects !== undefined) setExportMDFObjects(config.export_mdf_objects);
      if (config.export_fo_translations !== undefined) setExportFOTranslations(config.export_fo_translations);
      if (config.ec_objects) setSelectedECObjects(config.ec_objects);
      if (config.fo_translation_types) setSelectedFOTypes(config.fo_translation_types);
    }
  }, [currentProject]);

  // Load available objects
  useEffect(() => {
    const loadObjects = async () => {
      setIsLoading(true);
      try {
        const [ec, foTypes] = await Promise.all([
          projectsApi.export.getECObjects(),
          projectsApi.export.getFOTranslationTypes(),
        ]);
        setEcObjects(ec);
        setFoTranslationTypes(foTypes);
      } catch (error) {
        console.error('Failed to load export objects:', error);
      } finally {
        setIsLoading(false);
      }
    };
    loadObjects();
  }, []);

  const handleLocaleToggle = (locale: string) => {
    if (locale === 'en_US') return; // en_US is mandatory

    setSelectedLocales((prev) =>
      prev.includes(locale) ? prev.filter((l) => l !== locale) : [...prev, locale]
    );
  };

  const handleSelectAllLocales = () => {
    setSelectedLocales(AVAILABLE_LOCALES.map((l) => l.code));
  };

  const handleDeselectAllLocales = () => {
    setSelectedLocales(['en_US']); // Keep en_US mandatory
  };

  const handleECObjectToggle = (objectId: string) => {
    setSelectedECObjects((prev) =>
      prev.includes(objectId) ? prev.filter((id) => id !== objectId) : [...prev, objectId]
    );
  };

  const handleFOTypeToggle = (typeId: string) => {
    setSelectedFOTypes((prev) =>
      prev.includes(typeId) ? prev.filter((id) => id !== typeId) : [...prev, typeId]
    );
  };

  const handleSave = async () => {
    setSaveSuccess(false);
    try {
      await updateConfig(projectId, {
        locales: selectedLocales,
        export_picklists: exportPicklists,
        export_mdf_objects: exportMDFObjects,
        export_fo_translations: exportFOTranslations,
        ec_objects: exportMDFObjects ? selectedECObjects : [],
        fo_translation_types: exportFOTranslations ? selectedFOTypes : [],
      });
      setSaveSuccess(true);
      setTimeout(() => setSaveSuccess(false), 3000);
    } catch (error) {
      console.error('Failed to save config:', error);
    }
  };

  const hasConnection = currentProject?.config?.sf_connection?.connected;
  const canConfigureAPI = hasConnection && (exportPicklists || exportMDFObjects || exportFOTranslations);

  // Group locales by group
  const groupedLocales = AVAILABLE_LOCALES.reduce((acc, locale) => {
    if (!acc[locale.group]) acc[locale.group] = [];
    acc[locale.group].push(locale);
    return acc;
  }, {} as Record<string, typeof AVAILABLE_LOCALES>);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="text-center">
        <Settings className="mx-auto h-12 w-12 text-sap-blue-500 mb-4" />
        <h3 className="text-lg font-medium text-gray-900">Export Configuration</h3>
        <p className="mt-2 text-sm text-gray-600">
          Select languages and configure what to include in your translation workbook.
        </p>
      </div>

      {isLoading ? (
        <div className="flex justify-center py-8">
          <Loader2 className="w-8 h-8 animate-spin text-sap-blue-500" />
        </div>
      ) : (
        <>
          {/* Language Selection */}
          <div className="card">
            <div className="card-header flex items-center justify-between">
              <div className="flex items-center">
                <Languages className="w-5 h-5 mr-2 text-sap-blue-500" />
                <h4 className="text-sm font-medium text-gray-900">Languages</h4>
              </div>
              <div className="flex space-x-2">
                <button
                  onClick={handleSelectAllLocales}
                  className="text-xs text-sap-blue-600 hover:text-sap-blue-800"
                >
                  Select All
                </button>
                <span className="text-gray-300">|</span>
                <button
                  onClick={handleDeselectAllLocales}
                  className="text-xs text-sap-blue-600 hover:text-sap-blue-800"
                >
                  Deselect All
                </button>
              </div>
            </div>
            <div className="card-body">
              <p className="text-xs text-gray-500 mb-4">
                Selected: {selectedLocales.length} language{selectedLocales.length !== 1 ? 's' : ''}
              </p>

              {Object.entries(groupedLocales).map(([group, locales]) => (
                <div key={group} className="mb-4">
                  <h5 className="text-xs font-semibold text-gray-700 mb-2">{group}</h5>
                  <div className="grid grid-cols-2 md:grid-cols-3 gap-2">
                    {locales.map((locale) => (
                      <label
                        key={locale.code}
                        className={`flex items-center p-2 rounded border cursor-pointer transition-colors ${
                          selectedLocales.includes(locale.code)
                            ? 'border-sap-blue-500 bg-sap-blue-50'
                            : 'border-gray-200 hover:border-gray-300'
                        } ${locale.code === 'en_US' ? 'opacity-75' : ''}`}
                      >
                        <input
                          type="checkbox"
                          checked={selectedLocales.includes(locale.code)}
                          onChange={() => handleLocaleToggle(locale.code)}
                          disabled={locale.code === 'en_US'}
                          className="mr-2"
                        />
                        <span className="text-xs">{locale.name}</span>
                        {locale.code === 'en_US' && (
                          <span className="ml-auto text-xs text-gray-500">(Required)</span>
                        )}
                      </label>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Export Options */}
          <div className="card">
            <div className="card-header">
              <h4 className="text-sm font-medium text-gray-900">Export Options</h4>
              {!hasConnection && (
                <p className="text-xs text-gray-500 mt-1">
                  Connect to SuccessFactors API to enable additional export options
                </p>
              )}
            </div>
            <div className="card-body space-y-4">
              {/* Picklists */}
              <label className="flex items-start cursor-pointer">
                <input
                  type="checkbox"
                  checked={exportPicklists}
                  onChange={(e) => setExportPicklists(e.target.checked)}
                  disabled={!hasConnection}
                  className="mt-1 mr-3"
                />
                <div className="flex-1">
                  <span className="text-sm font-medium text-gray-900">Export Picklists</span>
                  <p className="text-xs text-gray-500 mt-1">
                    Include all picklist values from data model files
                  </p>
                </div>
              </label>

              {/* MDF Objects */}
              <label className="flex items-start cursor-pointer">
                <input
                  type="checkbox"
                  checked={exportMDFObjects}
                  onChange={(e) => setExportMDFObjects(e.target.checked)}
                  disabled={!hasConnection}
                  className="mt-1 mr-3"
                />
                <div className="flex-1">
                  <span className="text-sm font-medium text-gray-900">Export MDF Objects</span>
                  <p className="text-xs text-gray-500 mt-1">
                    Include Employee Central object translations
                  </p>
                </div>
              </label>

              {/* EC Objects Selection */}
              {exportMDFObjects && (
                <div className="ml-6 p-3 bg-gray-50 rounded-md">
                  <p className="text-xs font-medium text-gray-700 mb-2">
                    Select EC Objects ({selectedECObjects.length} selected):
                  </p>
                  <div className="space-y-1 max-h-48 overflow-y-auto">
                    {ecObjects.map((obj) => (
                      <label key={obj.id} className="flex items-start text-xs cursor-pointer">
                        <input
                          type="checkbox"
                          checked={selectedECObjects.includes(obj.id)}
                          onChange={() => handleECObjectToggle(obj.id)}
                          className="mr-2 mt-0.5"
                        />
                        <div>
                          <span className="font-medium">{obj.name}</span>
                          <span className="text-gray-500 ml-1">({obj.id})</span>
                          <p className="text-gray-500">{obj.description}</p>
                        </div>
                      </label>
                    ))}
                  </div>
                </div>
              )}

              {/* FO Translations */}
              <label className="flex items-start cursor-pointer">
                <input
                  type="checkbox"
                  checked={exportFOTranslations}
                  onChange={(e) => setExportFOTranslations(e.target.checked)}
                  disabled={!hasConnection}
                  className="mt-1 mr-3"
                />
                <div className="flex-1">
                  <span className="text-sm font-medium text-gray-900">
                    Export Foundation Object Translations
                  </span>
                  <p className="text-xs text-gray-500 mt-1">
                    Include Foundation Object (FO) translation data
                  </p>
                </div>
              </label>

              {/* FO Translation Types Selection */}
              {exportFOTranslations && (
                <div className="ml-6 p-3 bg-gray-50 rounded-md">
                  <p className="text-xs font-medium text-gray-700 mb-2">
                    Select FO Translation Types ({selectedFOTypes.length} selected):
                  </p>
                  <div className="space-y-1">
                    {foTranslationTypes.map((type) => (
                      <label key={type.id} className="flex items-start text-xs cursor-pointer">
                        <input
                          type="checkbox"
                          checked={selectedFOTypes.includes(type.id)}
                          onChange={() => handleFOTypeToggle(type.id)}
                          className="mr-2 mt-0.5"
                        />
                        <div>
                          <span className="font-medium">{type.name}</span>
                          <span className="text-gray-500 ml-1">({type.object})</span>
                          <p className="text-gray-500">{type.description}</p>
                        </div>
                      </label>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* Summary */}
          <div className="p-4 bg-blue-50 border border-blue-200 rounded-md">
            <p className="text-sm text-blue-800">
              <strong>Configuration Summary:</strong>
            </p>
            <ul className="mt-2 text-xs text-blue-700 space-y-1">
              <li> {selectedLocales.length} language{selectedLocales.length !== 1 ? 's' : ''} selected</li>
              {canConfigureAPI && (
                <>
                  {exportPicklists && <li> Picklists enabled</li>}
                  {exportMDFObjects && <li> {selectedECObjects.length} EC object{selectedECObjects.length !== 1 ? 's' : ''} selected</li>}
                  {exportFOTranslations && <li> {selectedFOTypes.length} FO translation type{selectedFOTypes.length !== 1 ? 's' : ''} selected</li>}
                </>
              )}
            </ul>
          </div>

          {/* Save Button */}
          <div className="flex justify-between items-center pt-4 border-t">
            <div>
              {saveSuccess && (
                <span className="text-sm text-green-600 flex items-center">
                  <CheckCircle className="w-4 h-4 mr-2" />
                  Configuration saved successfully
                </span>
              )}
            </div>

            <button onClick={handleSave} disabled={isSaving} className="btn-primary">
              {isSaving ? (
                <>
                  <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                  Saving...
                </>
              ) : (
                <>
                  <Save className="w-4 h-4 mr-2" />
                  Save Configuration
                </>
              )}
            </button>
          </div>
        </>
      )}
    </div>
  );
}
