/**
 * TREXIMA v4.0 - WebSocket Service
 *
 * Socket.IO client for real-time progress updates.
 */

import { io, type Socket } from 'socket.io-client';
import type { ProgressUpdate, OperationResult } from '../types';

type ProgressCallback = (update: ProgressUpdate) => void;
type CompleteCallback = (result: OperationResult) => void;
type ErrorCallback = (error: { code: string; message: string }) => void;
type SavedCallback = (data: { project_id: string; saved_at: string }) => void;

class WebSocketService {
  private socket: Socket | null = null;
  private subscriptions: Map<string, Set<string>> = new Map();

  // Callbacks
  private progressCallbacks: Map<string, ProgressCallback[]> = new Map();
  private completeCallbacks: Map<string, CompleteCallback[]> = new Map();
  private errorCallbacks: Map<string, ErrorCallback[]> = new Map();
  private savedCallbacks: Map<string, SavedCallback[]> = new Map();

  /**
   * Connect to WebSocket server
   */
  connect(): Promise<void> {
    return new Promise((resolve, reject) => {
      if (this.socket?.connected) {
        resolve();
        return;
      }

      // Determine WebSocket URL
      const wsUrl = import.meta.env.VITE_WS_URL || window.location.origin;

      this.socket = io(wsUrl, {
        path: '/socket.io',
        transports: ['websocket', 'polling'],
        withCredentials: true,
        autoConnect: true,
      });

      this.socket.on('connect', () => {
        console.log('WebSocket connected:', this.socket?.id);
        resolve();
      });

      this.socket.on('connect_error', (error) => {
        console.error('WebSocket connection error:', error);
        reject(error);
      });

      this.socket.on('disconnect', (reason) => {
        console.log('WebSocket disconnected:', reason);
      });

      // Set up event listeners
      this.setupEventListeners();
    });
  }

  /**
   * Disconnect from WebSocket server
   */
  disconnect(): void {
    if (this.socket) {
      this.socket.disconnect();
      this.socket = null;
    }
    this.subscriptions.clear();
    this.progressCallbacks.clear();
    this.completeCallbacks.clear();
    this.errorCallbacks.clear();
    this.savedCallbacks.clear();
  }

  /**
   * Subscribe to project updates
   */
  subscribeToProject(projectId: string): void {
    if (!this.socket?.connected) {
      console.warn('WebSocket not connected. Call connect() first.');
      return;
    }

    this.socket.emit('subscribe_project', { project_id: projectId });

    if (!this.subscriptions.has(projectId)) {
      this.subscriptions.set(projectId, new Set());
    }
  }

  /**
   * Unsubscribe from project updates
   */
  unsubscribeFromProject(projectId: string): void {
    if (!this.socket?.connected) return;

    this.socket.emit('unsubscribe_project', { project_id: projectId });
    this.subscriptions.delete(projectId);
    this.progressCallbacks.delete(projectId);
    this.completeCallbacks.delete(projectId);
    this.errorCallbacks.delete(projectId);
    this.savedCallbacks.delete(projectId);
  }

  /**
   * Request operation cancellation
   */
  cancelOperation(projectId: string): void {
    if (!this.socket?.connected) return;
    this.socket.emit('cancel_operation', { project_id: projectId });
  }

  /**
   * Register progress callback for a project
   */
  onProgress(projectId: string, callback: ProgressCallback): () => void {
    if (!this.progressCallbacks.has(projectId)) {
      this.progressCallbacks.set(projectId, []);
    }
    this.progressCallbacks.get(projectId)!.push(callback);

    // Return unsubscribe function
    return () => {
      const callbacks = this.progressCallbacks.get(projectId);
      if (callbacks) {
        const index = callbacks.indexOf(callback);
        if (index > -1) {
          callbacks.splice(index, 1);
        }
      }
    };
  }

  /**
   * Register operation complete callback for a project
   */
  onComplete(projectId: string, callback: CompleteCallback): () => void {
    if (!this.completeCallbacks.has(projectId)) {
      this.completeCallbacks.set(projectId, []);
    }
    this.completeCallbacks.get(projectId)!.push(callback);

    return () => {
      const callbacks = this.completeCallbacks.get(projectId);
      if (callbacks) {
        const index = callbacks.indexOf(callback);
        if (index > -1) {
          callbacks.splice(index, 1);
        }
      }
    };
  }

  /**
   * Register error callback for a project
   */
  onError(projectId: string, callback: ErrorCallback): () => void {
    if (!this.errorCallbacks.has(projectId)) {
      this.errorCallbacks.set(projectId, []);
    }
    this.errorCallbacks.get(projectId)!.push(callback);

    return () => {
      const callbacks = this.errorCallbacks.get(projectId);
      if (callbacks) {
        const index = callbacks.indexOf(callback);
        if (index > -1) {
          callbacks.splice(index, 1);
        }
      }
    };
  }

  /**
   * Register project saved callback
   */
  onProjectSaved(projectId: string, callback: SavedCallback): () => void {
    if (!this.savedCallbacks.has(projectId)) {
      this.savedCallbacks.set(projectId, []);
    }
    this.savedCallbacks.get(projectId)!.push(callback);

    return () => {
      const callbacks = this.savedCallbacks.get(projectId);
      if (callbacks) {
        const index = callbacks.indexOf(callback);
        if (index > -1) {
          callbacks.splice(index, 1);
        }
      }
    };
  }

  /**
   * Check if connected
   */
  isConnected(): boolean {
    return this.socket?.connected ?? false;
  }

  /**
   * Set up socket event listeners
   */
  private setupEventListeners(): void {
    if (!this.socket) return;

    this.socket.on('progress_update', (data: ProgressUpdate) => {
      const callbacks = this.progressCallbacks.get(data.project_id);
      if (callbacks) {
        callbacks.forEach((cb) => cb(data));
      }
    });

    this.socket.on('operation_complete', (data: OperationResult) => {
      const callbacks = this.completeCallbacks.get(data.project_id);
      if (callbacks) {
        callbacks.forEach((cb) => cb(data));
      }
    });

    this.socket.on('error', (data: { project_id: string; code: string; message: string }) => {
      const callbacks = this.errorCallbacks.get(data.project_id);
      if (callbacks) {
        callbacks.forEach((cb) => cb({ code: data.code, message: data.message }));
      }
    });

    this.socket.on('project_saved', (data: { project_id: string; saved_at: string }) => {
      const callbacks = this.savedCallbacks.get(data.project_id);
      if (callbacks) {
        callbacks.forEach((cb) => cb(data));
      }
    });

    this.socket.on('subscribed', (data: { project_id: string }) => {
      console.log('Subscribed to project:', data.project_id);
    });

    this.socket.on('unsubscribed', (data: { project_id: string }) => {
      console.log('Unsubscribed from project:', data.project_id);
    });
  }
}

// Export singleton instance
export const wsService = new WebSocketService();
export default wsService;
